export function getJourneyContent(journey: string): JourneyContent {
  return journeyContent[journey] || journeyContent.miscarriage // Default fallback
}

export function getRandomArticle(journey: string) {
  const content = getJourneyContent(journey)
  const randomIndex = Math.floor(Math.random() * content.articles.length)
  return content.articles[randomIndex]
}

export function getRandomMeditation(journey: string) {
  const content = getJourneyContent(journey)
  const randomIndex = Math.floor(Math.random() * content.meditations.length)
  return content.meditations[randomIndex]
}
